import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../gorelova/gorelova_1.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Ирина Горелова</div>
        </div>
      <div className={"textBlock"}>
        <p>Мой очень напряженный ритм жизни не&nbsp;позволял раньше проводить много времени дома, с&nbsp;семьей. Одним из&nbsp;моих опасений было, как мы&nbsp;сможем так тесно взаимодействовать в&nbsp;течении долгого времени, ведь раньше собирались дома лишь ближе к&nbsp;вечеру. Но&nbsp;нам оказалось очень хорошо и&nbsp;комфортно. И&nbsp;оказалось, что не&nbsp;сложно сидеть дома.</p>
        <p>За&nbsp;это время я&nbsp;профессионально освоила очень много нового, связанного с&nbsp;дистанционным обучением, овладела методиками, ранее недоступными. Мечтала заняться ландшафтным дизайном, даже подобрала онлайн курс. Но&hellip; оказалось работа в&nbsp;новом дистанционном режиме занимает практически все мое время, поэтому отложила мечту до&nbsp;лета.</p>
        <p>Мое мнение, что во&nbsp;время онлайн уроков ученики не&nbsp;изменились, их&nbsp;активность, послушность, прилежность проявляется практически так&nbsp;же, как в&nbsp;школе во&nbsp;время традиционных уроков.</p>
        <p>К&nbsp;сожалению, я&nbsp;планировала читать, смотреть, онлайн путешествовать намного больше. Но&nbsp;все равно нашла возможность посмотреть несколько хороших фильмов. Например, &laquo;Зеленая книга&raquo; режиссера Питера Форелли, &laquo;Ван Гог.&nbsp;На&nbsp;пороге вечности&raquo;. Очень люблю этого художника. Вместе с&nbsp;сыном, шестиклассником перечитала повесть В. Железникова &laquo;Чучело&raquo;, открыла для себя совершенно новые аспекты, не&nbsp;те, на&nbsp;которые обращала внимание в&nbsp;подростковом возрасте. Открыла для себя радио &laquo;Арзамас&raquo; с&nbsp;очень интересными лекциями.</p>
        <p>Для общения с&nbsp;друзьями и&nbsp;родными использую все доступные сейчас форматы: видеозвонки, видеоконференции, телефон <nobr>и т. д.</nobr></p>
      </div>
    </article>
  </Layout>
)

export default IndexPage



